header {
  height: 15vh;
  background: -webkit-gradient(linear, left top, right bottom, from(#F5F6CE), to(#FA5858));
  background: -webkit-linear-gradient(top left, #F5F6CE, #FA5858);
  background: -o-linear-gradient(top left, #F5F6CE, #FA5858);
  background: linear-gradient(to bottom right, #F5F6CE, #FA5858);
}

header .titles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 3em;
  width: 100%;
  height: 100%;
  color: black;
  font-family: 'Galada', cursive;
  text-shadow: 0px 5px rgba(0,0,0,0.2);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

header .titles:hover {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  color: white;
}

header .linkTag {
  text-decoration: none;
}

.trashBin {
  min-height: 40vh;
  margin-top: 3em;
}

.trashBin .list {
  margin-left: auto;
  margin-right: auto;
}


